import React from 'react'
import {  Button } from 'antd';

const Feature = () => {
  const features = ['# ', ' #  ', '#  ', '# ', '#  HOT', '# ']
  
  return (
    <>
      {features.map((feature)=> {
        return(
        <React.Fragment key={feature} >
            <Button 
              type="primary" 
              style={{ 
                color: 'black',
                fontWeight: 'bold',
                backgroundColor: '#ddd',
                margin: '10px'

              }} 
              >
                {feature}
            </Button>
          </React.Fragment>
        )
      })}
    </>
  )
}

export default Feature
