import React from 'react'
import {useParams} from 'react-router-dom'
import { Col, Row, Button, Space  } from 'antd';
import {LoremIpsum} from 'react-lorem-ipsum'
import Feature from './Feature';
import { AiOutlineDoubleLeft, AiOutlineDoubleRight } from "react-icons/ai";
import './detail.css'

const Detail = () => {
  const review_style = {
    padding: '8px 0',
    margin: '0 auto',
    fontSize: '15px',
    fontWeight: 'bold',
    background: '#fdd835',
  };

  return (
    <div className='container' style={{ margin: '0 auto', width: '1400px'}}>
      <div className='side'>
      </div>
      <div className='mainImage' style={{ width:'550px', margin: '0 auto'}}>
        <AiOutlineDoubleLeft />
        <img src= '/img/ CWPOCFTYZ220109-100.jpg' alt=' CWPOCFTYZ220109-100'
        style= {{ width: '90%'}} />
        <AiOutlineDoubleRight /> 
        <p style= {{ position:'relative', textAlign: 'center', bottom: '-50px' }}></p>
      </div>

      <div className='description' style={{fontWeight: 'bold'}}> 
        :&nbsp;      (CWPOCFTYZ220109100) <br /><br />
        :&nbsp; 99999999<br /><br />
        :&nbsp;  <br /><br />
        :&nbsp; 200, 205, 210, 215, 220, 225  

      </div>

      <div className='feature' >
      <Space className='f_button' style={{width: '100%', display: 'flex', alignContent: 'center', padding: 0}} >
        <Feature />
      </Space>
      </div>

      <div className='purchase' style={{ textAlign: 'center', position: 'relative', right: '50px'}}>
        <Button type="primary" 
          htmlType="submit" 
          className="purchase-button" 
          style={{ 
            width: '100px', 
            height: '45px',
            fontWeight: 'bold', 
            color: 'black',
            background: "skyblue",
            }} >
            <a href="/"></a>
        </Button>
        <span>&nbsp;&nbsp;&nbsp;</span>
        <Button type="primary" 
          htmlType="submit" 
          className="cart-button"  
          style={{ 
            width: '100px', 
            height: '45px', 
            fontWeight: 'bold', 
            color: 'black',
            background: "#fdd835", 
            }} >
            <a href="/" style={{  fontWeight: 'bold', }}></a>
        </Button>
        <p>   .</p>
      </div>

      <div className='recommend'>
        <span style={{position: 'relative', left: '50px'}}>
              .&nbsp;&nbsp;
            <a href='/' style={{fontSize: '14px', color: 'blue', textDecoration: 'none'}}>
               
            </a>
        </span>
        <Row style={{ textAlign: 'center', display: 'flex', justifyContent: 'content'}} >
          <Col className="gutter-row" span={6} >
            <div style={review_style}>size</div>
            <LoremIpsum p={1}  avgWordsPerSentence={4}/>
          </Col>
          <Col className="gutter-row" span={6}>
            <div style={review_style}>Color</div>
            <LoremIpsum p={1}  avgWordsPerSentence={4}/>
          </Col>
          <Col className="gutter-row" span={6}>
            <div style={review_style}></div>
            <LoremIpsum p={1} avgWordsPerSentence={4}/>
          </Col>
          <Col className="gutter-row" span={6}>
            <div style={review_style}>  </div>
            <LoremIpsum p={1}  avgWordsPerSentence={4} />
          </Col>  
    </Row>
      </div>
    </div>
  )
}

export default Detail
